.commerce-plus-app {
  .navigate-bar {
    position: sticky;
    z-index: 2;
    display: flex;
    align-items: center;
    background: linear-gradient(176.9deg, #ffffff 5.31%, rgba(255, 255, 255, 0.55) 262.81%);
    padding: 14px 24px 14px 16px;
    border-bottom: 1px solid #d0d0d0;
    top: 0px;
    width: 100%;
    z-index: 2;
    box-sizing: border-box;
    font-weight: 600;
    font-size: 12px;
    color: #464646;
    min-height: min-content;
    max-height: 56px;

    svg {
      display: inline-block;
      width: 18px;
      height: 18px;
    }

    .back-button {
      all: unset;
      cursor: pointer;
      min-width: 52px;
      margin-right: 24px;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;

      svg {
        margin-bottom: -4px;
        margin-right: 2px;
        pointer-events: none;
      }
    }

    .scroll-top {
      all: unset;
      cursor: pointer;
      margin-left: auto;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;

      svg {
        transform: rotate(90deg);
        margin-bottom: -4px;
        margin-right: 4px;
        pointer-events: none;
      }
    }

    &-desc {
      font-weight: 400;
      color: #858585;
      text-align: center;
      width: 249px;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
      > span {
        font-weight: 700;
        font-family: inherit;
      }
    }
  }
}
