.task-new {
  height: 44px;
  display: flex;
  align-items: center;
  padding: 0px 14px;
  cursor: pointer;
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  -webkit-tap-highlight-color: transparent;

  &.suggestion {
    @media (min-device-width: 750px) {
      &:hover {
        .task-new-suggestion {
          color: var(--primary-color);
        }
        .task-new-icon-stroke {
          fill: var(--primary-color);
        }
      }
    }
  }

  &.title {
    &:hover {
      background-color: var(--primary-color-overlay);
      border-radius: 52px;
    }
  }

  &:last-child {
    border-bottom: none;
  }

  &.highlighted {
    background-color: var(--primary-color-overlay);
  }

  &.hide {
    opacity: 0;
    visibility: hidden;
    height: 0;
    overflow: hidden;
  }

  &-icon {
    animation: icon-animation 0.2s ease-out;
    animation-fill-mode: both;
    min-height: 24px;
    &:empty {
      margin-right: 0 !important;
    }

    &.primary {
      margin-right: 8px;
      img {
        width: 20px;
        height: 20px;
        object-fit: contain;
      }

      &.default {
        height: 20px;
        display: flex;
        align-items: center;
        width: 20px;
        svg {
          margin: 0 auto;
          display: block;
        }
      }
    }

    &.arrow {
      margin-left: auto;
      margin-right: 0;
      svg {
        width: 16px;
        height: 16px;
      }
    }
  }

  .right {
    margin-left: auto !important;
    margin-right: 0;
  }

  &-title {
    color: var(--primary-color);
    animation: text-animation 0.2s ease-out;
    animation-fill-mode: both;
    animation-delay: 0.1s;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
    font-weight: 600;
    font-size: 14px;
  }

  &-suggestion {
    color: var(--primary-color);
    animation: text-animation 0.2s ease-out;
    animation-fill-mode: both;
    animation-delay: 0.1s;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
    font-weight: 600;
    font-size: 14px;
    @media (min-device-width: 750px) {
      color: #595959;
    }
  }
}

@keyframes icon-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes text-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
