.hsl-pdp-description {
  &-content {
    line-height: 18px;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: #464646;
    padding-bottom: 16px;
    span {
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
    }
  }

  &-join-word {
    margin-left: -3px;
  }

  &-more-button {
    color: var(--primary-color);
    opacity: 0.9;
    text-decoration-line: underline;
    margin-left: 4px;
    cursor: pointer;
  }
}
