.hsl-pdp-header {
  display: block;
  width: 100%;
  padding: 16px 16px 8px 16px;
  box-sizing: border-box;

  &-brand {
    font-size: 14px;
    font-weight: 600;
    color: #464646;
    max-height: 48px;
    line-height: 17px;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin-bottom: 4px;
  }

  &-title {
    font-size: 14px;
    font-weight: 600;
    color: #464646;
    max-height: 48px;
    line-height: 17px;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin-block-start: 0em;
    margin-block-end: 4px;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-brand:empty,
  &-title:empty {
    display: none;
  }

  &-price-detail {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    margin-top: 8px;
  }

  &-price {
    margin-right: 6px;
    font-weight: 600;
    line-height: 14px;
    color: #1b1b1b;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-mrp {
    text-decoration-line: line-through;
    color: #858585;
    margin-right: 6px;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-discount {
    background: linear-gradient(90deg, #00899f 0%, #00a5bf 101.72%);
    border-radius: 8px;
    font-weight: 700;
    font-size: 11px;
    line-height: 15px;
    color: #fff;
    padding: 2px 4px;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-mrp:empty,
  &-discount:empty {
    display: none;
  }
}
