.comm-add-to-cart {
  min-width: 64px;
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

  &-loader {
    transform: scale(0.75);
    display: flex;
    justify-content: center;
  }

  &-button {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    color: #464646;
    cursor: pointer;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-update {
    display: inline-block;
    background: #fefefe;
    width: 20px;
    height: 20px;
    text-align: center;
    border-radius: 50%;
    box-sizing: border-box;
    cursor: pointer;
  }

  &-update.add-product {
    display: flex;
    align-items: center;
    margin-left: 12px;
  }

  &-update.remove-product {
    display: flex;
    align-items: center;
    margin-right: 12px;
  }

  &-quantity {
    font-weight: 700;
    font-size: 14px;
    color: #1b1b1b;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }
}
