.hsl-modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 10;
}
.hsl-modal.active .hsl-modal-overlay {
  opacity: 0.2;
}
.hsl-modal.active .hsl-modal-content {
  transform: translate3d(0, 0, 0);
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.1);
}
.hsl-modal.open-from-side .hsl-modal-content {
  max-width: 400px;
  transform: translate3d(-100%, 0, 0);
  height: 100%;
}
.hsl-modal.open-from-side .hsl-modal-header-icon {
  transform: rotate(180deg);
}
.hsl-modal.open-from-side .hsl-modal-overlay {
  z-index: 3;
}
.hsl-modal.open-from-side.active .hsl-modal-overlay {
  opacity: 0.5;
}
.hsl-modal.open-from-side.active .hsl-modal-content {
  transform: translate3d(0, 0, 0);
}

.hsl-modal-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0;
  transition: 0.2s ease-out;
}

.hsl-modal-content {
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 3;
  width: 100%;
  max-height: 100%;
  left: 0;
  bottom: 0;
  background-color: #fff;
  max-height: 100%;
  box-shadow: 0 -10px 20px rgba(0, 0, 0, 0);
  transform: translate3d(0, 100%, 0);
  transition: 0.4s cubic-bezier(0.175, 0.885, 0.32, 1);
}
.hsl-modal-content.fullscreen {
  height: 100%;
}
.hsl-modal-content.fullscreen .hsl-modal-content-body {
  flex: 1;
  overflow: hidden;
}

.hsl-modal-content-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #eee;
}
.hsl-modal-content-header-icon.fullscreen {
