.comm-plp {
  flex-grow: 1;
  overflow-y: auto;
  padding: 8px 10px 8px 10px;
  box-sizing: border-box;
  height: calc(100% - 45px);

  &-result-para {
    min-height: 8px;
    margin-block-end: 8px;
    padding-left: 32px;
    padding-right: 12px;
    text-align: center;
    max-width: 100%;
    font-weight: 400;
    font-size: 12px;
    color: #858585;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;

    &-bold {
      font-weight: 600;
      margin-left: 4px;
    }
  }

  &-continue-chat {
    text-align: center;
    width: 100%;
    margin: 40px 0px 40px 0px;

    p {
      font-weight: 400;
      font-size: 10px;
      line-height: 12px;
      color: #595959;
    }

    button {
      margin: auto;
      border-radius: 8px;
      max-width: max-content;
      padding: 8px;
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      background: var(--primary-color);
    }
  }
}
