.task {
  display: flex;
  align-items: center;
  padding: 12px;
  border-bottom: 1px solid #EEE;
  cursor: pointer;
  transition: 0.1s ease-out;

  &:last-child {
    border-bottom: none;
  }

  &:hover {
    background-color: rgba(#EEE,0.6);
  }
  
  &.highlighted {
    background-color: rgba(#EEE,0.6);
  }

  &-icon {
    animation: icon-animation 0.2s ease-out;
    animation-fill-mode: both;
    min-height: 24px;
    &:empty {
      margin-right: 0 !important;
    }

    &.primary {
      margin-right: 8px;
      img {
        width: 24px;
        height: 24px;
        object-fit: contain;
      }
    }

    &.arrow {
      margin-left: auto;
      margin-right: 0;
      svg {
        width: 16px;
        height: 16px;
      }
    }
  }

  &-title {
    font-weight: 500;
    color: #333;
    animation: text-animation 0.2s ease-out;
    animation-fill-mode: both;
    animation-delay: 0.1s;
  }
}

@keyframes icon-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes text-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}