.toast {
  position: fixed;
  top: -30px;
  visibility: hidden;
  z-index: 4;
  box-sizing: border-box;
  width: 100%;
  padding: 14px 16px;
  border-radius: 4px;
  color: #fefefe;
  line-height: 20px;
  transition: all 0.25s ease-in-out;

  &.active {
    top: 50px;
    visibility: visible;
    &.success {
      background-color: #0b9543;
    }
    &.error {
      background-color: #b51925;
    }
  }
}
