.languageSelectorBar {
  font-family: 'Noto Sans', sans-serif;
  font-size: 16px;
  font-weight: 600;
  padding: 16px;
  border: 1px solid #efefef;
  border-left: 0px;
  border-right: 0px;
  cursor: pointer;
  display: flex;
  align-items: center;
  column-gap: 8px;
  transition: all 0.4s ease;

  &:hover {
    background-color: #f2f8ff;
  }
}
