.hsl-pdp-review {
  padding-top: 0px;
  position: relative;

  &.hsl-pdp-com-section-title {
    padding-top: 16px;
  }

  &-content {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    overflow-x: hidden;
  }

  &-rating-block {
    border-radius: 8px;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    color: #464646;
    max-width: max-content;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;

    .vertical-separator {
      display: inline-block;
      width: 1px;
      background-color: #d5d5d5;
      height: 16px;
      margin: -2px 6px -3px;
    }

    > svg {
      margin-left: 4px;
      margin-bottom: -1px;
    }
  }

  &-item {
    padding: 0px 0px 16px;
  }

  &-item-title {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #000000;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-item-rating {
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    color: #464646;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;

    > svg {
      margin-left: 2px;
      margin-bottom: -1px;
    }
  }

  &-item-rating.yellow {
    fill: #cd8900;
  }

  &-item-rating.red {
    fill: #b51925;
  }

  &-item-time {
    font-weight: 500;
    font-size: 10px;
    line-height: 14px;
    color: #979797;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-item-meta {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 2px;
  }

  &-item-description {
    margin-block-start: 4px;
    margin-block-end: 0em;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  .comm-plus-loader {
    padding-bottom: 16px;
  }

  &-separator {
    position: absolute;
    height: 1px;
    background-color: #eff1f5;
    width: 386px;
    top: 48px;
    left: -16px;
  }
}

.hsl-pdp-reviews {
  &-container {
    margin-top: 15px;
  }
}
