@import '@src/const/styles/mixins';
@import '@src/const/styles/mixins_flex';
@import '@src/const/styles/constants';

.typing-indicator {
  // border: solid 1px #e6e6e6;
  color: $color-dark-grey;
  @include transition(ease all 0.3s);
  width: 100%;
  font-size: 12px;
  text-align: left;
  @include similar-border-radius($border-radius);
  display: inline-block;
  clear: both;
  position: relative;
  word-break: break-word;
  @include inline-flex();
  @include justify-content(center);
  @include align-items(center);
  justify-content: flex-start;
  
  padding: 0;
  overflow: hidden;
  opacity: 0;
  transform-origin: 0 100%;
  transition: 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  
  &.typing {
    opacity: 1;
  }
  
  &.mirrored {
    justify-content: flex-end;
  }

  &-text {
    display: inline-block;
    margin: 0 5px;
  }

  .typing-indicator-loader {
    @include flexbox;
    @include align-items(flex-end);
    @include justify-content(center);
    margin-right: -4px;
    &.mirrored {
      flex-direction: row-reverse;
    }

    .typing-loader-circle {
      position: relative;
      top: -4px;
      width: 4px;
      height: 4px;
      border-radius: 16px;
      margin-right: 4px;
      background-color: $color-dark-grey;
      animation: typing-indicator 1s ease-in-out infinite;
      animation-fill-mode: both;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: 0.2s;
      }
      &:nth-child(3) {
        animation-delay: 0.4s;
      }
    }
  }
}

@keyframes typing-indicator {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
