@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

.voice-input {
  cursor: pointer;
  &.listening &-waves {
    transition-delay: 0.4s;
  }
  &.listening &-icon {
    transform: translate3d(-50%, -30%, 0) scale3d(0.5, 0.5, 1);
    opacity: 0;
    overflow: visible;
    transition-delay: 0.1s;
    svg {
      overflow: visible;
    }
    #head {
      transform: scale3d(1, 1, 1);
      animation: disappear 0.5s ease-in-out;
      animation-fill-mode: forwards;
      transform-origin: 50% 60%;
    }

    #stand {
      transform: scale3d(1, 1, 1);
      animation: disappear2 0.5s ease-out;
      animation-fill-mode: forwards;
      animation-delay: 0.2s;
      transform-origin: 50% 55%;
    }
    @keyframes disappear {
      40% {
        transform: scale3d(1.2, 1.2, 1);
      }
      100% {
        transform: scale3d(0.4, 0, 0);
      }
    }

    @keyframes disappear2 {
      100% {
        transform: scale3d(0, 0, 0);
      }
    }
  }

  &:not(.listening) &-waves {
    opacity: 0;
  }

  &-icon {
    width: 56px;
    height: 56px;
    position: absolute;
    z-index: 2;
    top: 60%;
    left: 50%;
    opacity: 1;
    transform: translate3d(-50%, -100%, 0) scale3d(1, 1, 1);
    margin-left: 3px;
    border-radius: 100px;
    transition: 0.2s ease-out;
    transform-origin: 50% 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15);

    svg {
      position: absolute;
      width: 38px;
      height: 38px;
      margin: 16%;
    }

    #head,
    #stand {
      transform: scale3d(1, 1, 1);
    }

    &:hover {
      box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.25);
      transform: translate3d(-50%, -102%, 0) scale3d(1, 1, 1);
    }
    
    &:active {
      box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2);
      transform: translate3d(-50%, -98%, 0) scale3d(1, 1, 1);
    }
  }
}
