$size: 28px;
$animation: bounce-out 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;

@mixin flash-bg-animation($color, $label) {
  animation: flash-bg-#{$label} 0.3s ease-in-out both;
  @at-root {
    @keyframes flash-bg-#{$label} {
      50% {
        fill: $color;
      }
      100% {
        fill: #FFF;
        stroke: $color;
      }
    }
  }
}

@mixin flash-thumb-animation($color, $label) {
  animation: flash-thumb-#{$label} 0.3s ease-in-out both;
  @at-root {
    @keyframes flash-thumb-#{$label} {
      50% {
        fill: #FFF;
      }
      100% {
        fill: $color;
      }
    }
  }
}

.feedback-icon {
  width: $size;
  height: $size;
  cursor: pointer;

  svg {
    width: $size;
    height: $size;
    overflow: visible;
  }

  &.POSITIVE {
    #Oval { transition: 0.4s ease-out; };
    #Thumbs { transition: 0.4s ease-out; };
    @for $i from 1 through 15 {
      #sparkle-#{$i} {
        transform-origin: 50% 100%;
        fill: #FFF;
        fill: darken(#25b27c, 10);
        opacity: 0;
      }
    }
    #line-left, #line-middle, #line-right {
      transform-origin: 50% 100%;
      fill: #FFF;
      fill: darken(#25b27c, 10);
      opacity: 0;
    }
  }
  
  &.NEGATIVE {
    #Oval { transition: 0.4s ease-out; };
    #Thumbs { transition: 0.4s ease-out; };
  }

  &.COMMENT {
    animation: wiggle 0.5s ease-in-out both;
  }

  &.highlighted {
    &.POSITIVE, .POSITIVE + &.COMMENT {
      animation: spark 0.4s ease-out;
      svg, #Oval { border: none !important; stroke: none;}
      #Oval { @include flash-bg-animation(#25b27c, 'positive') }
      #Thumbs { @include flash-thumb-animation(#25b27c, 'positive') }
      
      @for $i from 1 through 15 {
        #sparkle-#{$i} {
          animation: $animation;
          animation-delay: -0.03s * $i;
        }
      }
      
      #line-left {
        animation: $animation;
        animation-duration: 0.5s;
        animation-delay: -0.09s;
      }
      
      #line-middle {
        animation: $animation;
        animation-duration: 0.5s;
        animation-delay: -0.12s;
      }
      
      #line-right {
        animation: $animation;
        animation-duration: 0.5s;
        animation-delay: -0.15;
      }
    }
    
    &.NEGATIVE, .NEGATIVE + &.COMMENT {
      svg, #Oval { border: none !important; stroke: none;}
      #Oval { @include flash-bg-animation(#b22525, 'negative') }
      #Thumbs { @include flash-thumb-animation(#b22525, 'negative') }
    }
  }
}

@keyframes bounce-out {
  0% {
    opacity: 0;
    transform: translate3d(0,0,0) scale3d(0.5,0.5,1);
  }
  
  25% {
    opacity: 0;
  }

  80% {
    opacity: 1;
  }
  
  100% {
    opacity: 0;
    transform: translate3d(0px, 18px,0) scale3d(2,2,1);
  }
}

@keyframes wiggle {
  0% {
    opacity: 0;
    transform: rotate(-30deg);
  }
  25% {
    opacity: 1;
    transform: rotate(30deg);
  }
  75% {
    transform: rotate(-30deg);
  }
  100% {
    transform: rotate(0);
  }
}
