.comm-plp-card {
  margin-bottom: 12px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden;
  background: #fefefe;
  box-shadow: 0px 1px 3px 1px rgba(70, 70, 70, 0.15);
  filter: drop-shadow(0px 1px 2px rgba(70, 70, 70, 0.3));
  border-radius: 4px 4px 4px 4px;
  position: relative;
  cursor: pointer;
  height: 118px;
  &:hover {
    box-shadow: 0px 6px 10px 4px rgba(27, 27, 27, 0.15);
    filter: drop-shadow(0px 2px 3px rgba(27, 27, 27, 0.3));
  }

  &:last-of-type {
    border-bottom: none;
  }

  &-discount-banner {
    position: absolute;
    top: 0%;
    left: 0px;
    background: linear-gradient(90deg, #00899f 0%, #00a5bf 101.72%);
    border-radius: 0px 8px 8px 0px;
    padding: 0.5px 4px;
    font-weight: 700;
    font-size: 10px;
    line-height: 15px;
    color: #fff;
  }

  &-img-container {
    width: 90px;
    height: 118px;
    background: #fff;
  }

  &-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &-main {
    flex: 1;
    align-self: stretch;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 12px;
  }

  &-info {
    padding: 0 8px 0 6px;
    > h3,
    > p {
      margin-block-start: 0px;
      margin-block-end: 0px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      line-clamp: 1;
      overflow: hidden;
      margin-bottom: 6px;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
    }

    > h3 {
      font-weight: 600;
      font-size: 14px;
      color: #464646;
      line-height: 17px;
    }

    > p {
      font-weight: 400;
      font-size: 12px;
      color: #464646;
      line-height: 15px;
    }
  }

  &-price-section {
    width: 100%;
    font-size: 13px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2px;

    font-weight: 600;
    line-height: 15px;

    &-price {
      color: #1b1b1b;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
    }

    &-mrp {
      color: #858585;
      text-decoration-line: line-through;
      margin-left: 8px;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
    }

    &-discount {
      color: #f6a400;
    }

    &-separate-dot {
      background: #d5d5d5;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      margin: 0px 8px;
    }
  }

  &-bottom-section {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    margin-top: 6px;
    height: 40px;
    border-top: 0.5px solid #efefef;
  }

  &-rating-section {
    font-weight: 600;
    font-size: 12px;
    width: 85px;
    height: 32px;
    display: flex;
    align-items: center;
    margin-left: 6px;

    > span:last-child {
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
    }

    .rating-block {
      display: inline-block;
      font-weight: 600;
      text-align: left;
    }

    .rate-star {
      display: inline-block;
      width: 10px;
      height: 10px;
      margin-left: 4px;
      margin-bottom: 0px;

      &.yellow {
        fill: #ffd37b;
      }

      &.red {
        fill: #ef8b93;
      }
    }
  }

  &-count-divider {
    display: inline-block;
    width: 1px;
    background: #d5d5d5;
    height: 13px;
    margin: 0px 6px -2px;
  }

  &-rating {
    display: inline-block;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-add-cart {
    transition: all 0.2s ease-in-out;
    border-left: 0.5px solid #efefef;
    text-align: center;
    padding: 8px 6px;
    height: 40px;
    width: 119px !important;
    background-color: var(--primary-color-overlay);

    svg {
      fill: var(--primary-color);
      transform: scale(0.8);
    }

    .comm-add-to-cart-button {
      width: 100%;
      font-size: 14px;
    }

    .comm-add-to-cart-quantity {
      color: #464646;
    }
  }

  &-add-cart:hover {
    transition: all 0.2s ease-in-out;
  }
}
