.ominiSheet {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #00000087;
  z-index: 10;

  .container {
    position: absolute;
    width: 100%;
    height: 178px;
    bottom: 0;
    box-sizing: border-box;
    background: #efefef;
    border-radius: 4px;
    padding: 16px;
    border-radius: 4px;
    animation: slide-in-from-bottom 0.2s ease-out;

    .topbar {
      display: flex;
      height: 22px;
      justify-content: space-between;
      .title {
        font-family: 'Noto Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 23px;
        text-align: center;
        color: #1b1b1b;
        margin-bottom: 16px;
      }
    }

    .items {
      display: flex;
      margin-top: 12px;
      height: 112px;
      .item {
        display: flex;
        cursor: pointer;
        height: 112px;
        flex-grow: 1;
        background: #fefefe;
        box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.07);
        border-radius: 4px;
        background-color: white;
        flex-direction: column;
        margin-right: 16px;
        align-items: center;
        justify-content: center;
        svg {
          height: 72px;
          width: 72px;
        }
        .text {
          font-family: 'Noto Sans';
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 17px;
          text-align: center;
          color: #0067dd;
          margin-bottom: 5px;
        }
      }
    }
    .item:last-child {
      margin: 0;
    }

    @keyframes slide-in-from-bottom {
      0% {
        opacity: 0;
        transform: translateY(100%);
      }

      100% {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
}
