@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

.language-switcher {
  border-radius: $border-radius;
  @include position-absolute-fill();
  overflow: hidden;
  max-width: 100vw;
  z-index: 5;

  &.mobile,
  .mobile {
    @include similar-border-radius(0);
  }

  &-overlay {
    @include position-absolute-fill();
    border-radius: $border-radius;
    background-color: #000;
    transition: 0.4s ease-out;
    opacity: 0;
    z-index: 2;
    &.active {
      opacity: 0.8;
    }
  }

  &-body {
    position: absolute;
    bottom: 0;
    z-index: 5;
    background-color: #fff;
    width: calc( 100% - 32px );
    height: auto;
    overflow-y: auto;
    @include flexbox;
    @include align-items(flex-end);
    transition: 0.6s cubic-bezier(0.175, 0.885, 0.32, 1);
    transform: translateY(100%);
    padding: 16px;
    padding-top: 48px;
    &.active {
      transform: translateY(0);
    }
  }

  &-loading {
    width: 100%;
    color: $color-dark-grey;
    @include flexbox;
    @include flex-direction(column);
    @include align-items(center);
    animation: opacity-in 0.3s ease-out forwards;
    &-icon {
      margin-bottom: 24px;
    }
  }

  &-success,
  &-fail {
    width: 100%;
    color: $color-dark-grey;
    animation: opacity-in 0.3s ease-out forwards;
    @include flexbox;
    @include flex-direction(column);
    @include align-items(center);

    &-icon {
      margin-bottom: 16px;
      width: 40px;
      height: 40px;
      svg {
        width: 100%;
        height: 100%;
      }
    }

    &-text {
      margin-bottom: 16px;
    }

    &-button {
      cursor: pointer;
      background-color: $primary-color;
      width: 100%;
      padding: 12px;
      border-radius: $border-radius;
      color: #fff;
      text-align: center;
      transition: 0.2s ease-out;
      box-sizing: border-box;
      &:hover {
        background-color: darken($primary-color, 10);
      }
    }
  }
}

@keyframes opacity-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
