.commerce-plus-app {
  padding-top: 0px;
  background-color: #fafafaab;
  position: relative;
  overflow-x: hidden !important;
  overflow-y: auto;
  -ms-overflow-style: scrollbar;
  overscroll-behavior: none;
  height: calc(100vh - 73px);
  font-family: inherit;

  display: flex;
  flex-flow: column nowrap;

  .loader-wrapper {
    text-align: center;
  }

  .comm-plus-loader {
    width: 100%;
    min-width: 36px;
    min-height: 36px;

    .circular-loader {
      margin: auto;
    }
  }
}

.mobile .commerce-plus-app {
  height: calc(100vh - 64px);
}
