@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

.debug-view {
  @include flexbox();
  @include flex-direction(column);
  @include flex(1);
  position: relative;
  overflow: hidden;
  width: calc(100vw - 12px);
  height: calc(100vh - 14px);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  margin: 4px;
  margin-right: 8px;
  margin-left: 8px;
  margin-bottom: 10px;
  background-color: #fff;

  opacity: 0;
  transform-origin: 50% 100%;
  transition: opacity 0.2s linear, transform 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-delay: 0.1s;
  will-change: opacity, transform;
  transform: scale3d(0.9, 0.9, 1) translate3d(0, 30px, 0);

  &.active {
    opacity: 1;
    transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
  }

  &-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 2;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: 0.2s ease-out;
    @include flexbox();
    @include align-items(center);
    @include justify-content(center);

    &.active {
      opacity: 1;
      pointer-events: auto;
      visibility: visible;
    }
  }

  @mixin mobile-only {
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    width: 100vw;
    height: 100vh;
    max-height: 100vh;
    max-width: 100vw;
    min-width: 100vw;
    border-radius: 0;
    box-shadow: none;
    margin: 0;
  }

  @media (max-device-width: 500px) {
    @include mobile-only();
  }

  @media (max-device-width: 1000px) and (orientation: landscape) {
    @include mobile-only();
  }

  display: flex;
  flex-direction: column;

  &-header {
    padding: 12px;
    min-height: 56px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
    // padding-right: 24px;
    &-text {
      color: #333;
      font-weight: 500;
    }

    &-icon {
      background-color: rgba(#333, 0.1);
      border-radius: 8px;
      padding: 8px;
      margin-left: 8px;
      cursor: pointer;
      svg {
        width: 14px;
        height: 14px;
        polygon {
          fill: #333;
        }
      }
    }
  }

  &-body {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    > ul {
      flex: 1;
      margin: 0 !important;
      padding: 12px !important;
      padding-bottom: 36px;
      height: 100%;
      overflow-y: auto;
      span {
        font-family: monospace;
      }
    }
  }
}
