.hsl-pdp-variants {
  ::-webkit-scrollbar-button {
    max-height: 4px;
    height: 4px;
  }

  .product-option {
    cursor: pointer;
    padding: 8px;
    background: #fefefe;
    background: #fefefe;
    border: 1px solid #d5d5d5;
    min-height: 30px;
    box-sizing: border-box;
    margin-right: 8px;
    text-align: center;

    > span {
      color: #464646;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
    }

    &.selected,
    &.selected:hover {
      border: 1.5px solid var(--primary-color);
    }

    &:hover {
      border: 0.5px solid #464646;
    }

    * {
      pointer-events: none;
    }
  }

  .product-option.disabled {
    cursor: not-allowed;
    > span {
      color: #d5d5d5;
    }

    &:hover {
      border: 1px solid #d5d5d5;
    }
  }

  .variant-title-bar {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: 8px;
  }

  .product-options-expand {
    content: '>';
    float: right;
    height: 18px;
    width: 18px;
    margin-right: 2px;
  }

  .comm-accordion-title {
    padding: 16px;
  }

  .comm-accordion-section-title {
    flex: 1;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;

    .variant-header-bar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-right: 12px;
      span {
        font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
      }
    }
  }

  .horizontal-variants {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: nowrap;
    list-style-type: none;
    overflow-x: auto;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 8px;
    padding: 0px 8px 4px 0px;
    margin-bottom: 4px;

    .product-option {
      min-width: max-content;

      span {
        min-width: max-content;
      }
    }

    &.with-image .product-option {
      min-width: max-content;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      row-gap: 4px;
      margin-right: 10px;
      flex-flow: column nowrap;
      border: 1px solid #fff;
      padding: 6px 2px;
      text-align: left;

      img {
        width: 60px;
        height: 60px;
        border: 1.5px solid #d5d5d5;
        border-radius: 2px;
        object-fit: contain;
      }

      &.selected img {
        border-color: var(--primary-color);
      }

      span {
        display: inline-block;
        max-width: 150px;
        word-break: break-word;
        align-self: flex-start;
        min-width: fit-content;
      }
    }
  }

  .vertical-variants {
    display: block;
    list-style-type: none;
    overflow-x: auto;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 8px;
    padding: 0px 0px 4px 0px;
    margin-bottom: 4px;

    .product-option {
      font-weight: 400;
      font-size: 14px;
      line-height: 22px;
      letter-spacing: -0.01em;
      color: #464646;
      padding: 16px 0px;
      border: none;
      border-bottom: 1px solid #eff1f5;
      text-align: left;
      margin: 0px;

      &:last-of-type {
        border-bottom: none;
      }

      span {
        flex-grow: 1;
        padding: 0 16px;
      }

      span {
        flex-grow: 1;
      }

      &.selected,
      &.selected:hover {
        border: 1.5px solid var(--primary-color);
      }

      &:hover {
        border: 1px solid #d5d5d5;
      }
    }

    &.with-image .product-option {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      column-gap: 16px;
      padding: 8px 0px;

      img {
        width: 60px;
        height: 60px;
        border: 1px solid #d5d5d5;
        border-radius: 2px;
        object-fit: contain;
        min-width: 60px;
      }

      &.selected img {
        border-color: var(--primary-color);
      }
    }
  }
}

.vertical-layout {
  .comm-accordion-title.open {
    padding-bottom: 12px;
  }
}

.horizontal-layout {
  .comm-accordion-item {
    padding: 0 16px;
  }
}
