.hsl-pdp-gen-info {
  &-list {
    padding-top: 8px;
    list-style-type: none;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0;
    padding-bottom: 16px;
  }

  &-item {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 6px 0px;
    font-size: 14px;
    line-height: 17px;
    color: #1b1b1b;
  }

  &-item:last-of-type {
    padding-bottom: 0px;
  }

  &-type {
    width: 35%;
    padding-right: 8px;
    font-weight: 600;
    text-align: left;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-description {
    width: 65%;
    text-align: left;
    font-weight: 400;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }
}
