  background-repeat: repeat;
  position: relative;
  overflow-x: hidden !important;
  overflow-y: auto;
  -ms-overflow-style: scrollbar;
  overscroll-behavior: none;

  @include flexbox;
  @include flex-direction(column);

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    padding-right: 16px;
    padding-bottom: 24px;
    display: block !important;
  }

  flex: 1;
  width: 100%;
  @include transition(ease all 0.3s);
  @include background-size(60%);
  @include background-position-y(0px);
  @include background-origin(content-box);
  z-index: 2;
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0px);
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-perspective: 1001;
  padding-top: 8px;
  &:after {
    transition: 0.3s padding;
    transition-delay: 0.1s;
    content: '';
    display: block;
    padding-bottom: 32px;
  }

  &.full-screen-mode {
    padding: 24px 5%;
    box-sizing: border-box;
  }

  &.v2 {
    &.taskbox-open {
      overflow: hidden;
      &:after {
        padding-bottom: 32px;
      }
    }
  }
}

.message-section-messages {
  margin-top: auto;
  padding: 0 8px;
  transform: translateY(0);
  transition: 0.4s ease-out;

  &.taskbox-open {
    margin-bottom: 12px;
  }
}

.message-section-message {
  animation: translate-in 0.2s cubic-bezier(0.215, 0.61, 0.355, 1) backwards;
  animation-delay: 0.1s;

  &-row {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 8px;
  }

  &-debug-cta {
    margin-right: 8px;
    cursor: pointer;
  }

  &-retry-cta {
    color: #f76b6b;
    font-size: 10px;
    letter-spacing: -0.1px;
    padding-right: 8px;
    display: flex;
    align-items: center;
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
    &-icon {
      margin-left: 4px;
      width: 12px;
      height: 12px;
      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &-row.from-user + &-row.from-business,
  &-row.from-business + &-row.from-user {
    margin-top: 16px;
  }

  &-row.from-user,
  &-row.mirrored.from-business {
    justify-content: flex-end;
  }
  &-row.mirrored.from-user,
  &-row.from-business {
    justify-content: flex-start;
  }
}

.quick-reply {
  &-wrapper {
    position: relative;
    clear: both;
    transition: 0.1s ease-out;
    transform-origin: 100% 100%;
    &.submitted {
      opacity: 0;
    }
  }

  &-stacked {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-right: -4px;
    padding-top: 16px;
    > div {
      margin-bottom: 12px;
    }
  }

  &-option {
    display: inline-flex;
    justify-content: center;
    text-align: center;
    box-sizing: border-box;
    border-radius: 8px;
    transition: 0.2s ease-out;
    cursor: pointer;
    min-width: 48px;
    margin-right: 8px;
    margin-bottom: 8px;

    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2);
    &:hover {
      box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
      transform: translateY(-2px);
    }

    &-text {
      font-size: 14px;
      padding: 8px;
      color: #fff;
      text-align: center;
      line-height: 14px;
      &.mirrored {
        direction: rtl;
        text-align: right;
      }
      &.v2 {
        font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
      }
    }
  }
}

.fixed-bottom-area {
  position: absolute;
  z-index: 2;
  bottom: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto !important;
  padding: $message-window-padding;
  background-color: transparent;
  background: -moz-linear-gradient(top, rgba(242, 242, 242, 0) 0%, rgba(248, 248, 248, 1) 50%);
  background: -webkit-linear-gradient(top, rgba(242, 242, 242, 0) 0%, rgba(248, 248, 248, 1) 50%);
  background: linear-gradient(to bottom, rgba(242, 242, 242, 0) 0%, rgba(248, 248, 248, 1) 30%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00f8f8f8', endColorstr='#f8f8f8',GradientType=0 );
  width: 96%;
  box-sizing: border-box;
  &.no-composer {
    bottom: 0;
  }

  &.full-screen-mode {
    padding: 8px 12px;
  }

  &.v2 {
    bottom: 132px;
    background: none;
    &.taskbox-hidden {
      bottom: 96px;
    }
  }
}

.voice-typing-indicator {
  .from-business {
    @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.08));
    position: relative;
    color: #ffffff;
    float: right;
    white-space: pre-line;
    overflow: visible;
    right: $message-window-padding;
  }
}

.message-section-loader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
  background-color: #fff;
  width: 100%;
  height: 100%;
  width: 100%;
  @include flexbox;
  @include flex(1 1 auto);
  flex: 1;
  opacity: 0;
  pointer-events: none;
  transition: 0.2s ease-out;

  &.active {
    opacity: 1;
  }
}

.loading-spinner {
  display: block;
  position: relative;
  width: 40px;
  height: 40px;
  margin: 45% auto;
  circle {
    transform-origin: 50% 50%;
    animation: sk-scaleout 1s infinite ease-in-out both;
  }
}

@keyframes sk-scaleout {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0);
  }
}

@keyframes translate-in {
  0% {
    opacity: 0;
    transform: translate3d(0, 50%, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
