.hsl-pdp-img-slide {
  position: relative;
  text-align: center;

  &-slide {
    pointer-events: none;
  }

  &-slide img {
    vertical-align: middle;
    height: 400px;
    object-fit: cover;
    width: 100%;
  }

  &-image {
    max-width: 228px;
    margin: auto;
  }

  &-slideshow {
    max-width: 100%;
    position: relative;
  }

  /* Next & previous buttons */
  &-controls {
    position: absolute;
    top: 55%;
    left: 0px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0px 8px;
    box-sizing: border-box;
  }

  &-prev,
  &-next {
    position: absolute;
    cursor: pointer;
    box-sizing: border-box;
    width: 24px;
    height: 24px;
    margin-top: -22px;
    transition: 0.6s ease;
    border-radius: 8px;
    user-select: none;
    padding: 6px;
    z-index: 2;
    background: #fefefe;
    border: 1px solid #d5d5d5;
    svg {
      pointer-events: none;
    }
  }

  &-prev {
    left: 16px;
  }

  &-next {
    right: 16px;
    transform: rotateY(180deg);
  }

  &-prev:hover,
  &-next:hover {
    box-shadow: 0px 4px 4px rgba(27, 27, 27, 0.15);
  }

  &-similar-rating {
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    max-width: 100%;
    bottom: 6px;
    left: 0px;
    padding: 0px 10px;
    box-sizing: border-box;
  }

  &-view-similar {
    background: #fff;
    border: 1px solid #d5d5d5;
    backdrop-filter: blur(2px);
    border-radius: 4px;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    display: flex;
    align-items: center;
    text-align: center;
    color: var(--primary-color);
    padding: 5px 8px;
    cursor: pointer;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
    > svg {
      margin-right: 8px;
      pointer-events: none;
    }
  }

  &-rating-block {
    background: #fff;
    border: 1px solid #d5d5d5;
    border-radius: 8px;
    padding: 8px 12px;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    color: #464646;
    margin-left: auto;

    .vertical-separator {
      display: inline-block;
      width: 1px;
      background-color: #d5d5d5;
      height: 16px;
      margin: -2px 6px -3px;
    }

    > svg {
      margin-left: 4px;
      margin-bottom: -1px;
    }
  }

  &-sec-separator {
    background: #d5d5d5;
    height: 1px;
    margin-left: -16px;
    margin-right: -16px;
  }

  /* The dots/bullets/indicators */
  &-dot-indicator {
    text-align: center;
    min-height: 16px;
    position: absolute;
    margin: auto;
    width: 100%;
    bottom: 52px;
  }

  &-dot-active {
    background: rgba(70, 70, 70, 0.89);
    border-radius: 4px;
    padding: 2px 6px;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #fff;
    min-width: min-content;
    min-height: 16px;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-dot:hover {
    background-color: var(--primary-color);
  }

  /* Fading animation */
  &-fade {
    animation-name: fade;
    animation-duration: 0.3s;
  }

  @keyframes fade {
    from {
      opacity: 0.4;
    }

    to {
      opacity: 1;
    }
  }

  &-show-full {
    position: absolute;
    background: #f2f2f2;
    width: 28px;
    height: 28px;
    border-radius: 28px;
    bottom: 16px;
    left: 20px;
  }

  &-show-full svg {
    margin: auto;
    margin-top: 6px;
  }

  &-cross-btn {
    display: none;
    position: fixed;
    right: 18px;
    top: 0px;
    cursor: pointer;
    font-weight: 800;
    font-size: 18px;
    margin-top: 22px;
    z-index: 2;
  }

  &.full-screen {
    position: fixed;
    top: 0;
    z-index: 2;
    height: 80vh;
    background: #fff;
    padding-top: 26px;
    padding-bottom: 70px;
    width: calc(100% - 32px);

    .hsl-pdp-img-slide-slideshow {
      height: calc(100% - 60px);
    }

    .hsl-pdp-img-slide-slide,
    .hsl-pdp-img-slide-slide img {
      height: 100%;
    }

    .hsl-pdp-img-slide-show-full {
      display: none;
    }

    .hsl-pdp-img-slide-cross-btn {
      display: block;
      z-index: 3;
    }
  }
}
