$header-height: 56px;
$composer-height: 47px;

.taskbox {
  position: absolute;
  width: 100%;
  bottom: 0;
  z-index: 3;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  pointer-events: none;
  padding-bottom: $composer-height;
  box-sizing: border-box;

  &.no-composer {
    padding-bottom: 4px;
  }

  &-title {
    padding: 8px 12px;
    line-height: 1.4;
    border-bottom: 1px solid #EEE;
  }

  &-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    // top: $header-height + $composer-height;
    background-color: #000;
    opacity: 0;
    transition: 0.2s ease-out;
    pointer-events: auto;

    &.active {
      opacity: 0.6;
    }
  }

  &-body-wrapper {
    pointer-events: none;
    transition: 0.4s cubic-bezier(0.175, 0.885, 0.32, 1);    
  }

  &-body {
    background-color: #FFF;
    border-radius: 16px;
    border-top: 1px solid #EEE;
    padding-bottom: 48px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    margin-bottom: -48px;
    pointer-events: auto;
  }

  &-tab-wrapper {
    padding: 10px;
    padding-top: 14px;
  }
  
  &-tab {
    width: 32px;
    height: 4px;
    margin: 0 auto;
    border-radius: 16px;
    background-color: #DDD;
  }

  &-tasks {
    // transform: translateY(-50%);
    overflow-y: hidden;
    // max-height: 55vh;

    &.showing-more {
      overflow-y: auto;
      overflow-y: overlay;
    }
  }

  &-show-more {
    display: flex;
    align-items: center;
    padding: 12px;
    cursor: pointer;
    transition: 0.1s ease-out;
    margin-top: auto;
    min-height: 48px;
    background-color: #fff;
    position: relative;
    border-top: 1px solid #EEE;
    pointer-events: auto;
    box-sizing: border-box;
    
    &:hover {
      background-color: rgba(#EEE,1);
    }

    &.highlighted {
      background-color: rgba(#EEE,1);      
    }

    &-count {
      width: 24px;
      height: 24px;
      margin-right: 9px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 12px;
      background-color: #DDD;
      font-weight: 500;
      color: #333;
      font-size: 12px;
    }
  
    &-icon {
      margin-left: auto;
      margin-right: 0;
      svg {
        width: 16px;
        height: 16px;
      }
    }
  
    &-title {
      font-weight: 500;
      color: #333;
    }
  }
}

@keyframes show-taskbox-initial {
  100% {
    transform: translate3d(0,0,0);
  }
}
