@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

.lanuage-wrapper {
  position: absolute;
  z-index: 6;
  width: 100%;
  bottom: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  box-sizing: border-box;
}

.language-switcher-v2 {
  background: white;
  z-index: 6;
  animation: slide-in-from-bottom 0.2s ease-out;

  @keyframes slide-in-from-bottom {
    0% {
      opacity: 0;
      transform: translateY(100%);
    }
  
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &-apply {
    z-index: 6;
    float: right;
    padding: 20px;
    position: absolute;
    right: 0;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
  }

  &-header {
    height: 54px;
    display: flex;
    align-items: center;
    border-top: 1px solid #F2F2F2;
    border-bottom: 1px solid #F2F2F2;
    margin-bottom: 8px;
    position: relative;

    &-icon {
      margin-left: 24px;
      margin-right: 10px;
    }

    &-text {
      font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
      font-weight: 600;
      font-size: 12px;
      line-height: 36px;
      color: #606060;
    }

    &-error {
      display: flex;
      float: right;
      align-items: center;
      position: absolute;
      right: 0;
      margin-right: 18px;

      &-apply {
        max-width: 160px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
        font-weight: normal;
        font-size: 11px;
        line-height: 36px;
        color: #A6A6A6;
      }

      &-api {
        font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
        font-weight: 500;
        margin-left: 4px;
        font-size: 11px;
        line-height: 15px;
        color: #E53838;
        padding-bottom: 2px;
      }
    }
  }

  &-options {
    overflow-y: auto;
    max-height: 230px;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
  }

  &-item {
    margin-left: 24px;
    display: flex;
    height: 50px;
    align-items: center;

    &-native {
      transition: 0.2s ease-in;
      color: #606060;
      font-weight: 600;
      font-size: 16px;
      line-height: 20px;
      font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
    }

    &-translation {
      transition: 0.2s ease-in;
      margin-left: 12px;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      color: #A6A6A6;
      font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
    }
  }
}

.language-switcher-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.9;
}
