@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

$size: 24px;

.circular-loader {
  position: relative;
  height: $size;
  width: $size;
  animation: circular-loader-1 4.8s linear infinite;

  &-main {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    height: $size;
    width: $size;
    clip: rect(0, $size, $size, $size / 2);
    animation: circular-loader-2 1.2s linear infinite;
  }

  &-pseudo {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    height: $size;
    width: $size;
    clip: rect(0, $size, $size, $size / 2);
    border: 3px solid $primary-color;
    border-radius: 50%;
    box-sizing: border-box;
    animation: circular-loader-3 1.2s cubic-bezier(0.77, 0, 0.175, 1) infinite;
  }
}

@keyframes circular-loader-1 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes circular-loader-2 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(220deg);
  }
}

@keyframes circular-loader-3 {
  0% {
    transform: rotate(-140deg);
  }
  50% {
    transform: rotate(-160deg);
  }
  100% {
    transform: rotate(140deg);
  }
}
