$primary-color: #0E4498;
$color-warning: #f1c40f;
$color-dark-grey: #333;

$border-radius: 8px;
$message-padding: 8px;
$message-window-padding: 8px;
$message-margin-unrelated: 16px;
$message-margin-related: 8px;

$message-margin-unrelated: 8px;
$message-margin-related: 4px;

$header-height: 48px;
$header-box-shadow: 0 0 4px rgba(0,0,0,0.11);

$footer-height: 48px;
$footer-box-shadow: 0 0 4px rgba(0,0,0,0.11);

$quick-reply-box-shadow: 0 2px 8px rgba(0,0,0,0.33);
$quick-reply-box-shadow-hover: 0 6px 16px rgba(0,0,0,0.3);
$carousel-box-shadow: 0 2px 8px rgba(0,0,0,0.1);
$widget-box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
$location-picker-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2);
$location-picker-box-shadow-inverted: 0 -4px 4px 0 rgba(0, 0, 0, 0.2);

$mobile-breakpoint: 450px;
