.actions {
  cursor: pointer;
  margin-right: 5px;

  &-wrapper {
    display: flex;
    align-items: center;
    height: 44px;
    margin-left: 10px;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &.expand {
      animation: expand 0.3s ease-out;
      animation-fill-mode: both;
    }

    @keyframes expand {
      0% {
        opacity: 0;
        transform: translateX(20px);
      }

      100% {
        opacity: 1;
        transform: translateX(0);
      }
    }

    &.collapse {
      cursor: pointer;
      animation: collapse 0.3s ease-out;
      animation-fill-mode: both;
    }

    @keyframes collapse {
      0% {
        opacity: 0;
        transform: translateX(-20px);
      }

      100% {
        opacity: 1;
        transform: translateX(0);
      }
    }

    &.taskbox-hidden {
      padding-top: 16px !important;
      border-top: 1px solid #dddddd;
    }
  }

  .tooltip {
    display: none;
  }

  &:hover {
    .tooltip {
      position: absolute;
      direction: rtl;
      z-index: 6;
      top: 0;
      display: block;
      &-arrow {
        border-bottom-color: transparent !important;
        border-right-color: transparent !important;
        border-left-color: transparent !important;
        border: 6px #464646 solid;
        margin-left: 6px;
        margin-top: -2px;
        width: 0px;
      }

      &-text {
        font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
        background-color: #464646;
        font-size: 14px;
        line-height: 16px;
        color: #ffffff;
        text-align: center;
        padding: 5px 8px;
        border-radius: 8px;
        margin-right: -22px;
        position: absolute;
        white-space: nowrap;
        bottom: 12px;
      }
    }
  }
}
