.prompt {
  &Container {
    position: relative;
    width: 85%;
    height: auto;
    background: rgba(255, 255, 255, 0.55);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    -moz-backdrop-filter: blur(8px);
    -o-backdrop-filter: blur(8px);
    border: 1px solid rgba(255, 255, 255, 0.65);
    border-radius: 4px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 10px;
    &.active {
      animation: show 250ms cubic-bezier(0.22, 1, 0.36, 1) forwards; //easeOutQuint
    }
    &.deactive {
      animation: hide 250ms cubic-bezier(0.64, 0, 0.78, 0) forwards; //easeInQuint
    }
  }
  @keyframes show {
    0% {
      transform: scale(0.3);
      opacity: 0;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  @keyframes hide {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    100% {
      transform: scale(0.3);
      opacity: 0;
    }
  }
}
