.conversation-feedback {
  padding: 24px 12px;
  padding-bottom: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  &-title {
    display: block;
    font-size: 20px;
    text-align: center;
    color: #333;
    margin-bottom: 12px;
    font-weight: 500;
    &.v2 {
      font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
    }
  }

  &-stars {
    letter-spacing: 12px;
    margin-bottom: 8px;
  }

  &-star {
    font-size: 32px;
    color: #c9c9c9;
    cursor: pointer;
    transition: 0.2s ease-out;
    &.active {
      color: #f39704;
    }
    margin-bottom: 12px;
  }

  &-description {
    font-size: 14px;
    color: #666;
    margin-bottom: 24px;
    &.v2 {
      font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
    }
  }

  &-input {
    margin-bottom: 18px;
    width: 100%;
    textarea {
      resize: none;
      width: 100%;
      min-height: 64px;
      border-radius: 8px;
      padding: 8px;
      border: 1px solid #DDD;
      outline: none;
      transition: border-color 0.2s ease-out;

      &:not(:focus) {
        border-color: #DDD !important;
      }
      &.v2 {
        font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
      }
    }
  }

  &-button {
    display: block;
    padding: 12px 24px;
    background-color: #2196f3;
    font-size: 14px;
    color: #fff;
    outline: none;
    border: none;
    margin: 0 auto;
    border-radius: 8px;
    &.v2 {
      font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
    }
  }
}
