.omniHeader {
  display: flex;
  margin-right: 12px;
  min-width: 42px;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  .item {
    z-index: 3;
    position: relative;
    cursor: pointer;

    svg {
      width: 42px;
      height: 43px;
    }
  }

  .item:nth-child(2) {
    left: -15px;
    z-index: 2;
  }

  .item:nth-child(3) {
    left: -30px;
    z-index: 1;
  }
}
