.comm-accordion {
  &.comm-accordion-sticky {
    height: max-content;
    position: relative;

    .comm-accordion-title {
      position: -webkit-sticky;
      position: sticky;
      top: 0;
      background-color: #fff;

      * {
        pointer-events: none;
      }
    }
  }

  &-item {
    overflow: hidden;
    transition: max-height 0.3s cubic-bezier(1, 0, 1, 0);
    height: auto;
    max-height: max-content;
  }

  &-item.collapsed {
    max-height: 0;
    height: 0;
    transition: max-height 0.35s cubic-bezier(0, 1, 0, 1);
  }

  &-title {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 16px;
    padding-bottom: 16px;

    .indicator {
      margin-top: 4px;
      transition: rotate 0.15s ease-out;
      transform-origin: center center;
    }

    &:hover,
    &.open {
      color: black;
    }

    &.open .indicator {
      transform: rotate(180deg);
      transition: rotate 0.15s ease-out;
      transform-origin: center center;
    }
  }

  &-content {
    padding: 1em 1.5em;
  }

  &-section-title {
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
    text-align: left;
    color: #464646;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }
}
