@import '@src/const/styles/mixins';
@import '@src/const/styles/mixins_flex';
@import '@src/const/styles/constants';

.timeline-message {
  width: 100%;
  min-width: 100%;
  display: block;
  text-align: center;
  @include flexbox;
  @include flex-direction(column);
  align-items: center;
  margin-bottom: $message-margin-unrelated;

  &-text {
    font-size: 10px;
    letter-spacing: -0.1px;
    text-align: center;
    color: #777;
    height: 24px;
    min-height: 24px;
    margin-top: 16px;
    line-height: 24px;
    padding: 1px 11px;
    background-color: #fff;
    display: inline-table;
    @include similar-border-radius(24px);
    margin-bottom: 8px;
    border: 1px solid #e6e6e6;
    &.v2 {
      font-family: "Noto Sans Devanagari", "Noto Sans Tamil", "Noto Sans", sans-serif !important;
    }
  }
}
