.hsl-pdp-actions {
  position: fixed;
  bottom: 0;
  max-width: 100%;
  color: #fff;
  z-index: 3;
  background: #fefefe;
  box-shadow: 0px -4px 5px #0000000f;
  width: 100%;
  height: 74px;

  &-container {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    column-gap: 8px;
    padding: 16px;
    max-width: 100%;
    height: 42px;
  }

  &-btn {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 14px;
    outline: none;
    font-weight: 600;
    outline: none;
    border-radius: 8px;
    height: 42px;
    box-sizing: border-box;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif;
  }

  &-primary-btn {
    border: var(--primary-color) 1px solid;
    background-color: var(--primary-color);
    color: #fff;
    cursor: pointer;
  }

  &-secondary-btn {
    border: var(--primary-color) 1px solid;
    background-color: #fff;
    color: var(--primary-color);
    cursor: pointer;
  }

  &-btn.comm-add-to-cart {
    flex: 1;
    height: auto;
    max-height: 42px;
    box-sizing: border-box;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    outline: none;
    color: var(--primary-color);
    background-color: #fff;
    border: var(--primary-color) 1px solid;
    border-radius: 8px;
    justify-content: space-around;

    .comm-add-to-cart-button,
    .comm-add-to-cart-update,
    .comm-add-to-cart-quantity {
      color: inherit !important;
      font-weight: inherit !important;
      font-size: inherit !important;
      line-height: inherit !important;
    }

    .comm-add-to-cart-button {
      width: 100%;
    }

    .comm-add-to-cart-update {
      background: transparent;
      fill: #fff;
    }

    &.hsl-pdp-actions-primary-btn {
      color: #fff;
      background-color: var(--primary-color);
      border: var(--primary-color) 1px solid;
    }
  }
}
