@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

.bot-prompt-minimal-wrapper {
  width: 100%;
  height: 100%;
  display: inline-flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 0px 16px;
  // padding-top: 24px;
  // padding-right: 14px;
  // padding-left: 48px;
  // padding-bottom: 16px;
  box-sizing: border-box;

  &.center-mode {
    align-items: center;
    padding-bottom: 24px;
  }

  &.bottom-left-mode {
    align-items: flex-start;
  }
}

.bot-prompt-minimal {
  cursor: pointer;
  position: relative;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  border-radius: $border-radius;
  // margin-bottom: 12px;
  // margin-top: 16px;
  filter: drop-shadow(0 4px 8px rgba(#000, 0.1));
  transition: 0.2s ease-out;
  &:hover {
    filter: drop-shadow(0 4px 8px rgba(#000, 0.4));
  }

  &.active {
    &:after {
      transition-delay: 0.2s;
      opacity: 1;
    }

    .bot-prompt-minimal-bg {
      transform: translate3d(0, 0, 0) !important;
      transition-delay: 0s;
    }

    .bot-prompt-minimal-textarea {
      opacity: 1;
      transition-delay: 0.2s;
    }
    .bot-prompt-minimal-cross-icon {
      opacity: 1;
      transition-delay: 0.2s;
    }
  }

  &-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #fff;
    border-radius: $border-radius;

    transform: translate3d(100%, 0, 0);
    transform-origin: 100% 0;
    transition: 0.3s cubic-bezier(0.19, 1, 0.22, 1);
    transition-delay: 0.1s;
  }

  &-textarea {
    position: relative;
    color: #333;
    text-align: left;
    font-size: 14px;
    font-weight: 500;
    padding: 16px 20px;
    line-height: 1.3;
    opacity: 0;
    transition: 0.2s ease-out;
    max-width: 200px;
    &-text {
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
    }

    @media (max-device-width: 450px) {
      padding: 12px 20px;
      font-size: 14px;
    }
  }

  &-cross-icon {
    width: 10px;
    min-width: 10px;
    height: 10px;
    background-color: #fff;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    position: absolute;
    left: 26px;
    top: -6px;
    transform: translate3d(-100%, -100%, 0);
    box-sizing: content-box;
    transition: 0.2s ease-out;
    opacity: 0;

    &:hover {
      transform: translate3d(-100%, -100%, 0) scale(1.1, 1.1);
    }
  }

  &-qr-cross-icon {
    width: 10px;
    min-width: 10px;
    height: 10px;
    background-color: #fff;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    position: absolute;
    transform: translate3d(0, -120%, 0);
    box-sizing: content-box;
    transition: 0.2s ease-out;
    right: 4px;

    &:hover {
      transform: translate3d(0, -120%, 0) scale(1.1, 1.1);
    }
  }

  $parent: &;

  &.bottom-left-mode {
    align-items: flex-start;

    &:before {
      right: auto;
      left: 23px;
      border-left-width: 1px;
      border-right-width: 20px;
    }

    &:after {
      right: auto;
      left: 24px;
      border-left-width: 0px;
      border-right-width: 20px;
    }

    #{$parent}-cross-icon {
      left: auto;
      top: -6px;
      right: -25px;
      transform: translate3d(-100%, -100%, 0);
      &:hover {
        transform: translate3d(-100%, -100%, 0) scale(1.1, 1.1);
      }
    }

    #{$parent}-bg {
      transform: translate3d(-100%, 0, 0);
    }
  }

  &.center-mode {
    &:before {
      right: auto;
      left: auto;
      border-left-width: 16px;
      border-right-width: 16px;
    }

    &:after {
      right: auto;
      left: auto;
      border-left-width: 14px;
      border-right-width: 14px;
    }

    #{$parent}-cross-icon {
      left: auto;
      right: -12px;
      transform: translate3d(100%, -50%, 0);
    }
  }
}

.prompt-quick-reply {
  &-wrapper {
    position: relative;
    clear: both;
    transition: 0.1s ease-out;
    &.submitted {
      opacity: 0;
    }
  }

  &-stacked {
    display: block;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-right: -4px;
    padding-top: 12px;

    @media (max-device-width: 450px) {
      padding-top: 12px;
    }

    > div:not(.bot-prompt-minimal-qr-cross-icon) {
      margin-bottom: 6px;
    }
  }

  &-option {
    display: flex;
    justify-content: center;
    text-align: center;
    box-sizing: border-box;
    border-radius: 4px;
    cursor: pointer;
    min-width: 48px;
    margin-right: 8px;
    margin-bottom: 8px;
    height: 34px;
    margin-left: auto;
    width: fit-content;
    width: -moz-fit-content;
    width: -webkit-fit-content;
    max-width: 280px;
    transform: translate3d(100%, 0, 0);
    transform-origin: 100% 0;
    transition: 0.3s ease-out;
    position: relative;

    &:last-child {
      margin-bottom: 0px !important;
    }

    &.active {
      transform: translate3d(0, 0, 0);

      &.bottom-left-mode {
        transform: translate3d(0, 0, 0);
      }
    }

    &:hover {
      box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
      transform: translate(0, -2px);
    }

    &-text {
      font-size: 14px;
      padding: 4px 14px 5px 14px;
      color: #fff;
      text-align: center;
      line-height: 25px;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
      font-weight: 600;
    }

    &.bottom-left-mode {
      transform: translate3d(-100%, 0, 0);
      transform-origin: -100% 0;
      margin-left: unset;
    }
  }
}
