@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

.composer {
  padding-top: 20px;
  width: 100%;
  height: $footer-height;
  z-index: 5;
  position: relative;
  box-shadow: $footer-box-shadow;
  padding: 16px 10px;
  box-sizing: border-box;
  @include flexbox;
  @include align-items(center);
  @include justify-content(space-between);
  background-color: #fff;
  &.mirrored {
    flex-direction: row-reverse;
  }

  &.v2 {
    padding: 16px 10px 16px 12px;
    transition: background-color 200ms linear;
    box-shadow: none;
    height: 44px;
    @media (min-device-width: 750px) {
      &:hover {
        border: 2px solid var(--primary-color) !important;
        border-radius: 52px !important;
        background-color: var(--background-color) !important;
      }
    }
  }

  &.voice {
    width: 85% !important;
    margin-right: 8px;

    &.full {
      width: 100% !important;
    }

    &.listening {
      width: 100% !important;
      margin-right: 0px !important;
      background-color: var(--primary-color) !important;
      border: 2px solid var(--primary-color) !important;
    }
    &.voice-only:not(.listening) {
      display: none;
    }
  }

  &.full-screen-mode {
    padding: 8px 12px;
    .left-icon-area {
      display: none;
    }
  }

  &-wrapper {
    &.v2 {
      z-index: 5;
      background-color: #ffffff;
    }

    &.voice-only {
      background-color: #fafafa !important;
      border-top: 1px solid #f2f2f2;
    }

    &.taskbox-hidden {
      padding-top: 16px !important;
      border-top: 1px solid #dddddd;
    }

    &-voice {
      &.v2 {
        position: relative;
        display: flex;
      }
    }
  }

  &-branding {
    margin-top: 11px;
    align-items: center;
    display: flex;
    flex-direction: column;
  }

  &-icon {
    width: 24px;
    min-width: 24px;
    height: 24px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    svg {
      width: 100%;
      height: 100%;
      path {
        transition: 0.2s ease-out;
      }
    }

    &.mirrored {
      transform: scale(-1, 1);
    }

    &.left {
      margin-right: 8px;
    }

    &.right {
      margin-left: 8px;
    }

    &-voice {
      cursor: pointer;
      &:hover {
        .composer-icon-voice-border {
          stroke: var(--primary-color);
        }
      }
    }
  }

  &-text-area {
    @include appearance(none);
    cursor: text;
    background: #ffffff;
    border: none;
    width: 100%;
    padding: 12px 0;
    resize: none;
    outline: none;
    box-sizing: border-box;
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
    font-size: 14px;
    text-align: left;
    cursor: pointer;
    @include transition(ease all 0.3s);
    overflow: hidden;

    &::placeholder {
      font-style: italic;
      color: #bebebe;
    }

    &.v2 {
      background-color: transparent;
      overflow: auto;
      height: 44px;
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;

      ::-webkit-scrollbar {
        display: none;
        width: 0px;
        background: transparent; /* make scrollbar transparent */
      }

      &::placeholder {
        color: var(--placeholder-color);
        font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
        font-style: normal;
        font-weight: normal;
        opacity: 1;
      }

      &.mirrored {
        text-align: right;
        direction: rtl;
      }
    }
  }
}

.composer-area-voice {
  @include flexbox;
  @include align-items(center);
  @include justify-content(space-between);
  transition: 0.2s ease-out;
  transition-delay: 0.3s;

  .icon-area {
    position: relative;
    width: 28px;
    height: 28px;
    @include flexbox;
    @include align-items(center);
    @include justify-content(center);
    svg {
      width: 100%;
      height: 100%;
    }
  }

  .left-icon-area {
    width: 20px;
    height: 20px;
    padding-right: 0;
    @include transition(ease all 0.3s);
    transition-delay: 0.2s;
  }

  .right-icon-area {
    top: 0;
  }

  &.mic-open .left-icon-area {
    opacity: 0;
  }
}

.composer-voice-area {
  flex: 1;
  margin: 0 16px;
  margin-left: 0;

  textarea {
    border: none !important;
  }

  &-wrapper {
    -webkit-tap-highlight-color: transparent;
    display: block;
    width: 100%;
    transition: 0.3s ease-out;
  }
}

.composer-taskboxCTA {
  padding-top: 20px;
  width: 100%;
  margin-top: 2px;
  border-top: none;
  height: $footer-height;
  z-index: 5;
  position: relative;
  padding: 16px 18px;
  box-sizing: border-box;
  @include flexbox;
  @include align-items(center);
  @include justify-content(space-between);
  background-color: #fff;
  cursor: pointer;
  transition-delay: 0.25s;
  &-text {
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
    font-size: 14px;
    font-weight: 600;
  }
}
