$header-height: 64px;

.header-v2-wrapper {
  width: 100%;
  height: $header-height;
  z-index: 20;
}

.header-v2 {
  position: absolute;
  z-index: 4;
  width: 100%;
  height: $header-height;
  transition: 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  background-color: #fafafa;
  border-bottom: 1.5px solid #f2f2f2;

  &-top {
    height: $header-height;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 14px 10px 12px;
    box-sizing: border-box;
  }

  &-top-left {
    display: flex;
    align-items: center;

    opacity: 1;
    transition: 0.1s ease-out;
    transition-delay: 0.2s;

    &-image {
      width: 42px;
      height: 42px;
      border-radius: 32px;
      background-color: #fff;
      margin-right: 8px;
      overflow: visible;
      img {
        width: 100%;
        height: 100%;
        min-width: 42px;
        min-height: 42px;
        object-fit: contain;
      }
    }

    &-text {
      color: #595959;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      &-title {
        font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
        font-size: 12px;
        font-weight: 600;
        line-height: 19px;
      }
      &-credentials {
        font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
        font-size: 11px;
        line-height: 15px;
      }
    }
  }

  &-top-right {
    display: flex;
    justify-content: flex-end;

    &-cross {
      background-color: #ffffff;
      border-radius: 50%;
      display: flex;
      align-items: center;
      margin-left: 8px;
      cursor: pointer;
      width: 38px;
      height: 38px;
      -webkit-tap-highlight-color: transparent;
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -khtml-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;

      svg {
        margin: 0 auto;
        width: 20px;
        height: 20px;
      }
    }

    &-language {
      position: relative;
      display: inline-block;

      &-icon {
        display: flex;
        align-items: center;
        width: 38px;
        height: 38px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 8px;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;

        svg {
          margin: 0 auto;
        }
      }

      &-tooltip {
        direction: rtl;
        position: absolute;

        &-arrow {
          border-top-color: transparent !important;
          border-right-color: transparent !important;
          border-left-color: transparent !important;
          border: 6px var(--primary-color) solid;
          margin-left: 12px;
          margin-top: -2px;
          width: 0px;
        }

        &-text {
          font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
          visibility: visible;
          background-color: var(--primary-color);
          font-size: 12px;
          line-height: 16px;
          color: #ffffff;
          text-align: center;
          padding: 5px 8px;
          border-radius: 8px;
          margin-right: -22px;
          position: absolute;
          white-space: nowrap;
        }
      }
    }
  }

  &-message {
    padding: 24px;
    font-size: 16px;
    color: #fff;
    text-align: center;
    opacity: 0;
    transition: 0.1s ease-out;
    padding-top: 5%;

    animation: promo-in 0.2s ease-out;
    animation-fill-mode: both;
    animation-delay: 0.35s;
  }

  &-loader {
    width: 100%;
    height: 100%;
    filter: brightness(5) grayscale(1);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 16px;
  }

  &-cart {
    position: relative;
    cursor: pointer;
    &-count {
      position: absolute;
      width: 18px;
      height: 18px;
      background: #fcb61a;
      font-size: 10px;
      top: 0px;
      right: 0px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
