@import '@src/const/styles/mixins';
@import '@src/const/styles/mixins_flex';

$header-height: 56px;
$composer-height: 47px;
$footer-height: 48px;

.taskbox-new {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  pointer-events: none;
  box-sizing: border-box;

  &-compressTasks {
    border-radius: 24px;
    padding: 6px 16px;
    border: 1px solid var(--primary-color-border-overlay);
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--primary-color);
    transition: all 0.2s;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &:hover {
      @media (hover: hover) and (pointer: fine) {
        background-color: var(--primary-color-overlay);
        cursor: pointer;
        border-color: transparent;
        -webkit-tap-highlight-color: transparent;
      }
    }
    font-size: 12px;
    font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
    &-iconWrapper {
      margin-top: 3px;
      margin-left: 1.8px;
    }
  }
  &-compressTaskWrapper {
    position: absolute;
    bottom: 0px;
    background: white;
    width: 100%;
    padding: 12px 0px 12px 0px;
  }

  &-compressTaskSubWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &.no-composer {
    padding-bottom: 4px;
  }

  &-wrapper {
    z-index: 4;
  }

  &-CTA {
    padding-top: 20px;
    width: 100%;
    margin-top: 2px;
    border-top: none;
    height: $footer-height;
    z-index: 5;
    position: relative;
    box-sizing: border-box;
    transition: 0.2s ease-out;
    @include flexbox;
    @include align-items(center);
    @include justify-content(space-between);
    -webkit-tap-highlight-color: transparent;
    background-color: #fff;
    cursor: pointer;

    // hover for web
    @media (min-device-width: 750px) {
      &:hover {
        .taskbox-new-CTA-icon {
          stroke: var(--primary-color) !important;
        }
        span {
          color: var(--primary-color) !important;
        }
      }
    }

    &.mirrored {
      flex-direction: row-reverse;
    }

    &.active {
      @media (max-device-width: 500px) {
        .taskbox-new-CTA-text {
          color: #595959 !important;
        }
        .taskbox-new-CTA-icon {
          stroke: #595959 !important;
        }
      }
    }

    &-text {
      font-family: 'Noto Sans Devanagari', 'Noto Sans Tamil', 'Noto Sans', sans-serif !important;
      font-size: 14px;
      font-weight: 600;
      color: #595959;
      @media (max-device-width: 500px) {
        color: var(--primary-color) !important;
      }
    }

    &-icon {
      stroke: #595959;
      @media (max-device-width: 500px) {
        stroke: var(--primary-color) !important;
      }
    }
  }

  &-title {
    padding: 8px 12px;
    line-height: 1.4;
    border-bottom: 1px solid #eee;
  }

  &-overlay {
    z-index: 3;
    position: absolute;
    height: 0;
    width: 100%;
    opacity: 0;
    transition: 0.2s ease-out;
    pointer-events: auto;

    &.active {
      height: 100%;
      opacity: 0.6;

      &.language-selector-active {
        height: calc(100% - 53px);
      }
    }
  }

  &-body-wrapper {
    pointer-events: none;
    transition: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1);
  }

  &-body {
    z-index: 4;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    pointer-events: auto;
    max-height: 0px;
    overflow: scroll;
    transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    -ms-overflow-style: none; /* Internet Explorer 10+ */
    scrollbar-width: none;
    &.mirrored {
      direction: rtl;
    }
    &.active {
      max-height: 420px;
      margin-bottom: 13px;
    }
    &.activeWithCompressTasks {
      max-height: 364px;
      margin-bottom: 63px;
    }
  }
  &-body::-webkit-scrollbar {
    display: none;
  }

  &-tasks {
    padding: 0 10px;
  }
}

@keyframes show-taskbox-initial {
  100% {
    transform: translate3d(0, 0, 0);
  }
}
