.omniButton {
  box-sizing: border-box;
  width: 100%;
  height: 42px;
  padding: 10px 12px;
  border-radius: 4px;
  border: 0;
  outline: none;
  font-family: 'Noto Sans', 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  color: white;
  background: white;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;

  &:not(:last-child) {
    margin-bottom: 12px;
  }

  &.filled {
    &.whatsapp {
      background: #0db350;
    }
    &.instagram {
      background: linear-gradient(90deg, #eea03f -13.26%, #e13322 55.25%, #bd2773 168.41%);
    }
    &.messenger {
      background: #1877f2;
    }
  }
  &.outline {
    color: rgb(27, 27, 27);
    border: 1px solid #d5d5d5;

    &:hover,
    &:focus {
      color: white;
      border: none;
    }

    &.whatsapp {
      border-left: 6px solid #0db350;
      &:hover,
      &:focus {
        background: #0db350;
      }
    }
    &.instagram {
      border-left: 6px solid #c6019a;
      &:hover,
      &:focus {
        background: #c6019a;
      }
    }
    &.messenger {
      border-left: 6px solid #1877f2;
      &:hover,
      &:focus {
        background: #1877f2;
      }
    }
  }
  &.disabled {
    cursor: not-allowed;
    -webkit-filter: grayscale(95%);
    -moz-filter: grayscale(95%);
    -o-filter: grayscale(95%);
  }
}
