@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

$color_high: #58DA7C;
$color_medium: #EFD54F;
$color_low: #F76B6B;

$color_high: #FFF;
$color_medium: #FFF;
$color_low: #FFF;

.rating-prompt-wrapper {
    display: flex;
}

.rating-prompt {
    position: relative;
    background-color: #fff;
    box-shadow: 0 5px 20px rgba(0,0,0,0.2);
    border-radius: 12px;
    // width: 100%;
    height: 100%;
    padding: 16px;
    padding-bottom: 16px;
    min-width: 300px;
    margin: 8px;
    margin-top: 16px;

    @media (max-device-width: 450px) {
        bottom: 0;
        right: 0;
        width: 100vw;
        height: 100vh;
        min-width: 100vw;
        display: flex;
        flex-direction: column;
        background-color: rgba(0,0,0,0.5);
        animation: opacity-in 0.4s cubic-bezier(1, 0, 0, 1) both;
        text-align: center;
    }

    &-close-icon {
        position: absolute;
        top: 8px;
        right: 8px;
        cursor: pointer;
        z-index: 2;
        padding: 8px;
        transition: 0.2s ease-out;
        border-radius: 4px;
        &:hover {
            background-color: #EEE;
        }

        &:active {
            background-color: #DDD;
        }
    }

    @media (max-device-width: 450px) {
        position: relative;
        // padding-top: 40%;
        @include flexbox();
        @include flex-direction(column);
        @include align-items(center);
        @include justify-content(center);
        padding-top: 0;
        padding-bottom: 0;
        border-radius: 0;
        transition: 0.4s cubic-bezier(0.19, 1, 0.22, 1);
        transform: translate3d(0,100%,0);
        &.active {
            transform: translate3d(0,0,0);
        }
    }

    @media (min-width: 450px) {
        animation: slide-up-custom-2 0.4s cubic-bezier(1, 0, 0, 1) both;
    }

    &.full-screen {
        .rating-prompt-image, .rating-prompt-title, .rating-prompt-subtitle {
            margin-bottom: 24px;
        }
    }

    &.half-screen {
        height: auto;
        text-align: center;
        background-color: #fff;
        box-shadow: 0 5px 20px rgba(0,0,0,0.2);
        border-radius: 12px;
        padding: 32px 24px;
        padding-top: 42px;
        margin-top: auto;
        margin-bottom: 0;
        animation: slide-up-vertical 0.4s cubic-bezier(1, 0, 0, 1) both;
        animation-delay: 0.2s;

        @media (max-device-width: 450px) {
            position: relative;
            border-radius: 0;
        }

        &-rating-prompt-image {
            display: none;
        }
    }

    &-image {
        height: 90px;
        margin: 0 auto;
        margin-bottom: 24px;
        img {
            width: auto;
            height: 100%;
            object-fit: contain;
        }
    }

    &-title {
        font-size: 16px;
        margin: 0 auto;
        margin-bottom: 16px;
        max-width: 450px;
        text-align: center;
        line-height: 1.5;
    }

    &-subtitle {
        font-size: 16px;
        font-weight: 600;
        margin: 0 auto;
        margin-bottom: 16px;
        text-align: center;
        max-width: 500px;
        line-height: 1.5;
    }

    &.range-numbers {
        min-width: 560px;
        @media (max-device-width: 450px) {
            min-width: 0;
        }
        .rating-prompt-title, .rating-prompt-subtitle {
            text-align: left;
            max-width: 500px;
            margin-left: 0;
            @media (max-device-width: 450px) {
                text-align: center;
            }
        }
    }

    &.range-stars {
        padding: 8px;
        padding-top: 48px;
        padding-bottom: 24px;
        .rating-prompt-title, .rating-prompt-subtitle {
            max-width: 360px;
        }
        .rating-bar-items {
            justify-content: center;
            margin-bottom: 0;
        }

        .rating-bar-item {
            width: 48px;
            height: 48px;
            font-size: 22px;
        }
    }

    &.fixed-yes-no {
        .fixed-bar-item {
            width: auto;
            height: auto;
            border: 2px solid #39AEED;
            border-radius: 8px;
            padding: 8px 12px;

        }
    }
}

.rating-bar {
    margin: 0 auto;
    &-items {
        display: flex;
        flex-wrap: wrap;
        margin-right: -8px;
        margin-bottom: 8px;
        @media (max-device-width: 450px) {
            justify-content: center;
        }
    }

    &-item {
        width: 40px;
        height: 40px;

        @media (max-device-width: 450px) {
            width: 48px;
            height: 48px;
            font-size: 22px;
        }

        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        border-radius: 12px;
        margin-right: 8px;
        margin-bottom: 8px;
        font-weight: 600;
        color: #FFF;
        transition: 0.2s ease-out;
        cursor: pointer;
        border: 2px solid transparent;
        animation: fade-up 0.3s cubic-bezier(1, 0, 0, 1);

        color: #39AEED;
        border: 2px solid #39AEED;

        &:nth-child(2n) {
            animation-delay: 0.08s;
        }
        &:nth-child(3n) {
            animation-delay: 0.16s;
        }
        &:nth-child(4n) {
            animation-delay: 0.24s;
        }

        &:not(.star):hover {
            box-shadow: 0 4px 8px rgba(0,0,0,0.2);
            transform: translateY(-2px);
        }

        &:active {
            background-color: #fff !important;
            color: #39AEED;
            transform: translateY(0);
            border: 2px solid #39AEED;
        }

        &.low {
            background-color: $color_low;
        }
        &.medium {
            background-color: $color_medium;
        }
        &.high {
            background-color: $color_high;
        }

        &.star {
            border: none;
            #star {
                polygon {
                    transition: 0.2s ease-out;
                }
                .full {
                    fill: #F7A03F;
                }
            }
        }

    }

    &-legend {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-right: -24px;
        margin-top: 32px;
        @media (max-device-width: 450px) {
            justify-content: center;
            &.no-mobile {
                display: none;
            }
        }

        &-item {
            display: flex;
            align-items: center;
            font-size: 12px;
            font-weight: 700;
            margin-right: 24px;
            &-icon {
                // width: 12px;
                height: 24px;
                text-align: center;
                line-height: 24px;
                border-radius: 8px;
                color: #FFF;
                margin-right: 4px;
                color: #39AEED;
                // border: 2px solid #39AEED;
                &.low {
                    background-color: $color_low;
                }
                &.high {
                    background-color: $color_high;
                }
            }
            &-text {
                color: #999;
            }
        }
    }
}

.fixed-bar {
    display: flex;
    align-items: center;
    justify-content: center;
    &-items {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-right: -24px;
        min-width: 140px;
    }

    &-item-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &-item {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        width: 60px;
        height: 60px;
        border-radius: 100px;
        margin-bottom: 8px;
        font-weight: 600;
        color: #39AEED;;
        transition: 0.2s ease-out;
        cursor: pointer;

        &.thumb {
            svg {
                width: 100%;
                height: 100%;
            }
        }

        &-text {
            font-size: 12px;
            font-weight: 700;
            color: #999;
        }

        &:hover {
            box-shadow: 0 4px 8px rgba(0,0,0,0.2);
            transform: translateY(-2px);
        }

        &:active {
           transform: translateY(0);
        }

        &.low {
            background-color: #F76B6B;
        }

        &.high {
            background-color: #58DA7C;
        }

    }
}
