@import '@src/const/styles/mixins';
@import '@src/const/styles/constants';
@import '@src/const/styles/mixins_flex';

.voice-input-waves-v2 {
  @include flexbox;
  @include align-items(center);
  @include justify-content(center);
  transition: 0.2s ease-out;
  position: absolute;
  height: 100%;
  z-index: 10;
  width: 100%;
  left: 0;
  top: 0;

  &.branding-hidden {
    bottom: 4%;
  }
}

.voice-input-wave-v2 {
  width: 4px;
  height: 9px;
  border-radius: 100px;
  margin-right: 5px;
  &:nth-child(even) {
    height: 15px;
  }
  transition: 0.1s ease-out;
  animation: woozy 1s ease-in-out infinite;

  @for $i from 0 through 4 {
    &:nth-child(#{$i}) {
      animation-delay: 0.1s * $i;
    }
  }
}

@keyframes woozy {
  0% {
    transform: scale3d(1, 0.8, 1);
  }
  50% {
    transform: scale3d(1, 1.1, 1);
  }
  100% {
    transform: scale3d(1, 0.8, 1);
  }
}
